
import _ from 'lodash'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import { mapGetters, mapActions } from 'vuex'
export default {

props: {},
data() {
return {
isLoading: true,
isSubmitting: false,
item: {
category: [],
recordType: 0,
amount: 0,
memo: '',
desc: '',
datetime: dateFormat(new Date(), 'YYYY-MM-DD HH:mm')
},
}
},
components: {
Group,
PopupPicker,
Cell,
Loading,
ButtonTab,
ButtonTabItem,
XInput,
Datetime,
XTextarea,
Flexbox,
FlexboxItem,
XButton
},
mounted() {
if (this.categoryData.length == 0) {
this.isLoading = true;
this.getCategories(() => {
this.isLoading = false;
})
} else {
this.isLoading = false;
}
},
computed: {
...mapGetters(['categoryData'])
},
methods: {
checkData() {
if (this.item.category.length == 0) {
this.$vux.toast.show({
text: '请选择分类！',
type: 'warn',
});
return false;
}
if (this.item.amount == 0) {
this.$vux.toast.show({
text: '请输入金额！',
type: 'warn',
});
return false;
}
if (!this.item.datetime) {
this.$vux.toast.show({
text: '请选择时间！',
type: 'warn',
});
return false;
}
if (!this.item.desc) {
this.$vux.toast.show({
text: '请输入描述！',
type: 'warn',
});
return false;
}
return true;
},
onSubmit() {
if (this.checkData()) {
var item = _.assign({}, this.item, {
category: this.item.category[0]
});
this.saveRecord({
data: { item },
cb: res => {
if (res.Success) {
this.$vux.toast.show({
text: '新建成功！',
type: 'success',
});

}
}
});
}
},
reset() {
this.item = {
category: [],
recordType: 0,
amount: 0,
memo: '',
desc: '',
datetime: dateFormat(new Date(), 'YYYY-MM-DD HH:mm')
}
},
...mapActions(['getCategories', 'saveRecord'])
}
}




